#motionwrap {
    display: none;

    @media only screen and (min-width: 1024px) {
        display: block;
    }
}

.first-screen-mob {
    @media only screen and (min-width: 1024px) {
        display: none;
    }
}
.motion-logo {
    height: 340px;
    position: absolute;
    z-index: 1500;
    text-align: center;
    top: 35%;
    margin-top: -170px;
    left: 0;
    width: 100%;
    opacity: 0;
    transform: scale(0.6);
    transition: transform 0.6s, opacity 0.3s, height 0.5s;
    transition-timing-function: ease-out;
}

.motion-logo img {
    height: 100%;
}

.motion-logo.active {
    opacity: 1; 
    transform: scale(1);
}

.motion-title {
    display: none;
    position: absolute;
    z-index: 1500;
    font-size: 32px;
    font-weight: 100;
    text-align: center;
    color: #fff;
    top: 40%;
    left: 0;
    width: 100%;
    opacity: 0;
    transform: translate(0,7px);
    transition: transform 0.3s, opacity 0.3s;
    transition-timing-function: ease-in-out;
    transition-delay: 0.4s;

}

.motion-btns {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

    .btn {
        width: 300px;
    }

    .btn {
        margin-top: 30px;
    }

    @media only screen and (min-width: 768px) {
        .btn {
            width: 250px;
            margin-top: 0;
        }

        .btn + .btn {
            margin-left: 30px;
        }
    }
}

.motion-title.active {
    opacity: 1;
    transform: translate(0px);
}

.motion-logo h1 {
    color: #fff;
    text-transform: uppercase;
    font-size: 36px;
    margin-bottom: 40px;
    font-weight: 300;
}

.first-screen-mob {
    text-align: center;
    padding: 70px 10px;
    background-image: url(../img/main-bg-mob.png);
    background-size: cover;
    background-position: 50% 50%;
}

.first-screen-mob h1 {
    font-size: 16px;
    color: #fff;
    font-weight: 300;
    margin-bottom: 30px;
    text-transform: uppercase;
}

@media only screen and (max-width: 767px) {
    .btn1.btn1_s {
        width: 250px;
        font-size: 16px;
        padding: 0 30px;
        height: 50px;
        line-height: 50px;
    }

    .btn1_s + .btn1_s {
        margin-top: 20px;
    }
}

@media only screen and (min-width: 768px) {
    .btn1.btn1_s {
        width: 250px;
        font-size: 16px;
        padding: 0 30px;
        height: 50px;
        line-height: 50px;
    }
    
    .btn1_s + .btn1_s {
        margin-left: 20px;
        margin-top: 0;
    }

    .first-screen-mob {
        padding: 170px 10px;
    }
}

@media only screen and (min-width: 1024px) {
    .first-screen-mob {
        display: none;
    }
}

#motionwrap {
    position: relative;
    background: #333;
    height: 2000px;
}

.motion {
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-color: #333; /*remove this property later*/
    position: fixed;
    z-index: 1000;
    left: 0;
    transition: opacity 0.5s;
    opacity: 0;
}

#mossland-ar {
    position: absolute;
    top: 0;
    display: none;
    background-color: #383b34;
}

#mossland-reality {
    position: absolute;
    top: 0;
}

#loading {
   position: absolute;
    left: 0;
    z-index: 1100;
    background: #333;
    opacity: 0;
    transition: opacity 0.6s;
    display: none;
    background: url("../img/loading.jpg") center center no-repeat;
    background-size: cover;
}

#loading > div {
    display: block;
    position: absolute;
    width: 100%;
    height: 50%;
    left: 0;
    top: 50%;
    color: #7e7e7e;
    text-align: center;
}

#loading.active {
    opacity: 1;
    display: block;
}



@keyframes spinner {
    to {transform: rotate(360deg);}
}

.spinner:before {
    content: '';
    box-sizing: border-box;
    position: absolute;
    top: 0;
    left: 50%;
    width: 30px;
    height: 30px;
    margin-left: -15px;
    border-radius: 50%;
    border: 1px solid #ccc;
    border-top-color: #07d;
    animation: spinner .6s linear infinite;
}

